@import url(https://fonts.googleapis.com/css?family=Montserrat);
* {
  box-sizing: border-box;
}

@media only screen and (max-width: 750px) {
  .menu-bar {
    overflow-x: auto;
    overflow-y: hidden;
    width: calc(100% - 10px);
  }
  .hero-image {
    width: calc(100% - 10px);
    height: auto;
  }
}

body {
  /* background-color: #28223F; */
  background-color: #fff;
  font-family: Montserrat, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
}

h3 {
  margin: 10px;
  height: 46px;
  text-overflow: ellipsis;
}

h6 {
  margin: 5px 0;
  text-transform: uppercase;
}

p {
  font-size: 14px;
  line-height: 21px;
}

.hero-image {
  height: 100px;
  /* width: -webkit-fill-available; */
}

.container {
  display: flex;
  flex-flow: wrap;
  margin-top: 150px;
  margin-bottom: 50px;
  justify-content: center;
}

.card-container {
  /* background-color: #231E39; */
  background-color: #1c3862;
  border-radius: 5px;
  box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
  color: #b3b8cd;
  padding-top: 30px;
  position: relative;
  width: 320px;
  max-width: 100%;
  text-align: center;
  margin: 10px;
}

.subscribe {
  width: 360px;
}

.card-container .pro {
  color: #231e39;
  background-color: #febb0b;
  border-radius: 3px;
  font-size: 14px;
  font-weight: bold;
  padding: 3px 7px;
  position: absolute;
  top: 30px;
  left: 30px;
}

.card-container .round {
  border: 1px solid #03bfcb;
  border-radius: 50%;
  padding: 7px;
  width: 128px;
  height: 128px;
  object-fit: cover;
}

.section-header {
  width: 100%;
  font-size: x-large;
  margin: 10px 0 10px 35px;
  font-weight: bolder;
  text-decoration: none;
  color: black;
}

button.primary {
  background-color: #03bfcb;
  border: 1px solid #03bfcb;
  border-radius: 3px;
  color: #231e39;
  font-family: Montserrat, sans-serif;
  font-weight: bolder;
  padding: 10px 25px;
  margin: 0 10px 0 10px;
  cursor: pointer;
}
button.primary:hover {
  background-color: transparent;
  color: #fff;
}

button.primary.ghost {
  background-color: transparent;
  color: #fff;
}
button.primary.ghost:hover {
  background-color: #03bfcb;
  color: #231e39;
}

.card-buttons {
  /* background-color: #1F1A36; */
  background-color: #0b4870;
  text-align: center;
  padding: 15px;
  margin-top: 20px;
}

.card-buttons ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.card-buttons ul li {
  border: 1px solid #2d2747;
  border-radius: 2px;
  display: inline-block;
  font-size: 12px;
  margin: 0 7px 7px 0;
  padding: 7px;
}

header {
  background-color: #f9fafb;
  color: #fff;
  font-size: 14px;
  top: 0;
  position: fixed;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 999;
  height: 150px;
  /* display: flex;
    flex-flow: wrap; */
}

footer {
  background-color: #222;
  color: #fff;
  font-size: 14px;
  bottom: 0;
  position: fixed;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 999;
}

footer p {
  margin: 10px 0;
}

footer i {
  color: red;
}

footer i.white {
  color: white;
}

footer a {
  color: #3c97bf;
  text-decoration: none;
  margin: 0 5px 0 0;
}

.menu-bar {
  border-radius: 25px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: inline-flex;
  background-color: #cce9ff;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  align-items: center;
  padding: 0 10px;
  margin: 0px;
  /* width: 100%; */
  /* overflow-x: auto; */
}
.menu-bar li {
  list-style: none;
  color: #1c3862;
  font-family: sans-serif;
  font-weight: bold;
  padding: 12px 16px;
  margin: 0 8px;
  position: relative;
  cursor: pointer;
  white-space: nowrap;
}
.menu-bar li a {
  text-decoration: none;
  color: #1c3862;
}
.menu-bar li:before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  transition: 0.2s;
  border-radius: 25px;
}
.menu-bar li:hover::before {
  background: linear-gradient(to bottom, #cce9ff, #cce9ff);
  box-shadow: 0px 3px 20px 0px black;
  transform: scale(1.2);
}

.has-dropcap:first-letter {
  font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
  float: left;
  font-size: 6rem;
  line-height: 0.65;
  margin: 0.1em 0.1em 0.2em 0;
}

.text {
  width: 50%;
  margin: 20px 0 10px 0;
  text-align: justify;
  color: #1c3862;
}

.width100 {
  width: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

